import React, { useState, useEffect, useContext, useId, useRef } from 'react';
import Draggable from 'react-draggable';
import { Context } from '../../context/IdContext';

export const Rented = ({ w, className, rented, index, ...props }) => {
	const { state: { id }, idMsg } = useContext(Context);
	const [ desc, setDesc ] = useState(false);
	const [ pos, setPos ] = useState({ x: 0, y: 0 });
	const [ width, setWidth ] = useState(0);
	const [ paddingLeft, setPaddingLeft ] = useState(0);

	const rentedRef = useRef();
	useEffect(() => {
		if (rentedRef.current !== null) {
			const RR = rentedRef.current.getBoundingClientRect();

			if (RR.x < 0) {
				// console.log(RR.x);
				setPaddingLeft(-RR.x);
			}
			setWidth(window.innerWidth - RR.x);
		}
	}, []);

	const nextId = useId();
	// setPos({ x: e.clientX, y: e.clientY })
	return (
		<Draggable grid={[ 100, 40 ]}>
			<div
				// onMouseMove={handlePos}
				ref={rentedRef}
				key={id}
				onMouseOver={() => idMsg(rented)}
				onMouseOut={() => idMsg(false)}
				className={className}
				style={{
					display: 'flex',
					justifyContent: 'flex-start',
					cursor: 'pointer',
					width: `${rented.width / 100 * (w * 2)}px`
				}}
			>
				<div
					style={{
						width: `${Math.round(width)}px`,
						display: 'flex',
						justifyContent: 'center'
						// backgroundColor: 'orange'
					}}
				>
					<div
						style={{
							paddingLeft: `${Math.round(paddingLeft)}px`
						}}
					>
						{rented.name}
					</div>
				</div>
			</div>
		</Draggable>
	);
};
